	module.exports = {"data":{"markdownRemark":{"html":"<h3>Invitations</h3>\n<p>We’ve improved the way programs can manage their invitations to hackers. You can now set a report volume target where we’ll monitor and manage your hacker invitations to help you meet your report goal.</p>\n<p>The Invite Hackers tab under Settings > Program > Hacker Management has been renamed to Invitations.  </p>\n<p>The Invitations page includes the new Report Volume field where you can enter the number of reports you'd like to receive in 30 days.</p>\n<p><a href=\"/hackers/invitations.html\">Read more about Invitations</a>.</p>\n<h3>Needs More Info</h3>\n<p>Reports in the Needs More Info state that haven’t been responded to within 30 days automatically get closed with no negative impact to the hacker’s reputation.</p>\n<h3>Self-Controlled Launch</h3>\n<p>Response Programs in Controlled Launch that meet all of the success criteria are now prompted to publicly launch their own program through following the Setup Guide or through email notification.</p>\n<h3>Response SLA Settings</h3>\n<p>Response SLA settings are now applied to all reports and not just reports created after modification to SLA settings.</p>\n<p>Response SLA settings are also now incorporated into Controlled Launch for Response programs. Programs must’ve received at least 10 reports and invited 100 hackers while maintaining healthy responsive times before launching publicly.</p>\n<h3>Program Health Dashboard</h3>\n<p>The new Program Health Dashboard helps programs track their Response Efficiency Metrics and Response SLA performance. Go to Dashboard > Program Health to view your metrics.</p>\n<h3>Response Efficiency Indicator</h3>\n<p>Programs can now see their response efficiency indicator in their program dropdown. This enables them to see their response efficiency status without having to visit their security page.</p>\n<h3>Bug Fixes</h3>\n<ul>\n<li>Invite notifications don’t show up again for expired, declined, and duplicate invites.</li>\n<li>The questions on the Invitation Rejection Questionnaire and the Leave Program Questionnaire no longer show duplicates.</li>\n<li>The Time to Bounty timer now pauses when a report is closed as either N/A, Duplicate, Informative, or Spam.</li>\n<li>The red response efficiency indicator tooltip now correctly states that the program has failed SLAs instead of missed SLAs.</li>\n</ul>","frontmatter":{"path":"/changelog/2018/march","title":"March 2018"}}},"pathContext":{}}

